<script>
	export let players = [];
	export let roomCode;

	import ConnectivityIndicator from './ConnectivityIndicator.svelte';
	import { fly, fade } from 'svelte/transition';
	import { flip } from 'svelte/animate';
	import { socket } from '../connectivity.js';

	$: points = players.filter(player => player.connectionId == socket.id)[0]
		.points;

	function selectAndCopy() {
		this.select();
		const range = document.createRange();
		range.selectNodeContents(this);
		const selection = window.getSelection();
		selection.removeAllRanges();
		selection.addRange(range);
		this.setSelectionRange(0, 99999);
		document.execCommand('copy');
	}
</script>

<style>
	.header {
		margin-bottom: 0.5em;
		display: flex;
		align-content: center;
		align-items: center;
	}

	.count {
		font-weight: bold;
		font-size: 4em;
		flex: 1;
	}

	.players {
		flex: 1;
	}

	.player {
		display: flex;
		transition: opacity 0.3s ease-in-out;
	}

	.player strong {
		flex: 1;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	.player span {
		padding-left: 1em;
	}

	.connectivity-wrapper {
		float: right;
	}

	.more a,
	.more input {
		width: 100%;
		display: block;
		box-sizing: border-box;
		border: 0;
		background: none;
		margin: 0.5em 0 0 0;
		padding: 0.5em 0 0 0;
		border-radius: 0.3em;
		text-decoration: none;
		text-align: right;
	}

	.more a {
		text-transform: uppercase;
	}
</style>

<div class="header">
	<div class="count">{points}</div>
	<div class="connectivity-wrapper">
		<ConnectivityIndicator />
	</div>
</div>

<div class="players">
	{#each players
		.filter(player => player.connected)
		.sort((a, b) => b.points - a.points) as player (player.connectionId)}
		<div class="player" animate:flip={{ duration: 300 }}>
			<strong transition:fly={{ x: -20, duration: 300 }}>
				{player.name}
			</strong>
			<span transition:fade={{ duration: 300 }}>{player.points}</span>
		</div>
	{/each}
</div>

<div class="more">
	<a href="./">new game &rarr;</a>
	<input
		type="text"
		value={window.location.href.split('?')[0] + '?room=' + roomCode}
		on:click={selectAndCopy} />
</div>
